import { createUserWithEmailAndPassword } from "firebase/auth";
import { auth } from "./firebaseConfig";
import React, { useState } from "react";

const SignUp = ({ onSignUp, onToggleLogin }) => {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [error, setError] = useState("");

  const handleSignUp = async (e) => {
    e.preventDefault();
    setError(""); // Clear previous errors
    try {
      await createUserWithEmailAndPassword(auth, email, password);
      onSignUp(); // Redirect to the app after successful sign-up
    } catch (error) {
      //  Enhanced Error Handling Based on Firebase Error Codes
      switch (error.code) {
        case "auth/email-already-in-use":
          setError(" Email already in use. Try logging in.");
          break;
        case "auth/invalid-email":
          setError(" Invalid email format. Please correct it.");
          break;
        case "auth/weak-password":
          setError(" Password should be at least 6 characters long.");
          break;
        default:
          setError(" Something went wrong. Please try again.");
      }
    }
  };

  return (
    <div className="signup-container container mt-5">
      <h2 className="text-center"> Sign Up for Fitness Tracker</h2>
      {error && <p className="text-danger text-center">{error}</p>}
      <form onSubmit={handleSignUp} className="col-md-4 offset-md-4">
        <div className="form-group mb-3">
          <input
            type="email"
            className="form-control"
            placeholder="Email"
            value={email}
            onChange={(e) => setEmail(e.target.value)}
            required
          />
        </div>
        <div className="form-group mb-3">
          <input
            type="password"
            className="form-control"
            placeholder="Password"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            required
          />
        </div>
        <button type="submit" className="btn btn-success w-100">Sign Up</button>
      </form>

      {/* Link to toggle back to the Login form */}
      <p className="text-center mt-3">
        Already have an account?{" "}
        <button
          type="button"
          className="btn btn-link p-0"
          onClick={onToggleLogin}
        >
          Login
        </button>
      </p>
    </div>
  );
};

export default SignUp;
